a.current {
    color: #d41443;
  }

.menu__modal {
    padding-top: 60px;
    
    background-color: #e17992;
    position: absolute;
    width: 100vw;
    z-index: 2;
    height: 100vh;
    @media screen and (min-width: 768px) {
        width: 30vw;  
        right: 0;
    }
    @media screen and (min-width: 1280px) {
                display: none;
    }

}

.menu__ul {
    margin-top: 32px;
    margin-bottom: -22px;
    
}
.menu__link {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.27;
    
    padding-top: 10px;
    padding-bottom: 10px;
    color: #ffffff; 
    &:focus, &:hover {
        color: #d41443;
    }
    
}
.menu__li {
   border-top: 1px solid #e18298;
   padding-left: 22px;
   margin-bottom: 22px;
   
}

.menu__li:last-child {
    border-bottom: 1px solid #e18298;
    
}
.menu__btn {
    fill: #d41443;
    border: none;
    background-color: transparent;
    @media screen and (min-width: 1279px) {
        display: none;
    }
    
    
}
.menu__btn--main {
    background: #ffffff;
    border-radius: 22px;
    
    display: flex;
    justify-content: center;
    align-items: center;
    

    border: none;
    color: #d41443;

    margin-left: 30px; 
   
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 15px;
    padding-bottom: 15px;

    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;

    &:focus, &:hover {
        background-color: #ecebeb;
    }
    




}
.menu__icon--right {
    fill: #d41443;
    margin-left: 28px;
}
.menu__close {
    background-color: transparent;
    border: none;
    position: absolute;
    top: 23px;
    right: 28px;
    z-index: 10;
    
}
.menu__close-icon {
    fill: #ffffff;
    background-color: transparent;
    width: 18px;
    height: 18px;
}







.header {
    background-color: #ffb8ca;  
    overflow: hidden;

}



.hh__container {    
    position: relative;
    z-index: 1;
    @media screen and (max-width: 767px) {
        padding-top: 8px; 
        min-height: 500px;
    }
    @media screen and (min-width: 768px) {
        padding-top: 38px;  
    }
}
.hh__wrap {
display: flex;
justify-content: space-between;
@media screen and (min-width: 1280px) {
    display: none;
}
}
.hh__wrap-a {
display: flex;
}

.logo {
    display: flex;
    align-items: center;
    @media screen and (min-width:1280px) {
        margin-right: 110px;
    }
}
.logo__icon {
    fill: #ffffff;
    background-color: #d41443;
    border-radius: 50%;
}
.logo__span {
    font-family: 'Titan One';
    font-style: normal;
    font-size: 26px;
    line-height: 1.15;
    font-weight: 400;
    text-transform: uppercase;
    color: #d41443;
    margin-left: 16px;
    
}
.hh__nav {
    display: none;
    @media screen and (min-width: 1280px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.hh__ul {
    display: none;
    @media screen and (min-width: 1280px) {
        display: flex;
        
    }
}
.hh__li {
    @media screen and (min-width: 1280px) {
    
    &:not(:last-child) {
        margin-right: 35px;
    }
    }
}
.hh__link {
    @media screen and (min-width: 1280px) {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.27;
    
    color: #ffffff;  
    
    }
    &:focus, &:hover {
        color: #d41443;
    }
}
.hh__btn {
    
    display: none;

@media screen and (min-width: 768px) {
    background: #ffffff;
    border-radius: 22px;
    display: flex;
    justify-content: center;
    

    padding-left: 40px;
    padding-right: 40px;
    padding-top: 15px;
    padding-bottom: 15px;

        }

        &:focus, &:hover {
            background-color: #ecebeb;
        }

}
.hh__box {
    display: flex;
    justify-content: space-between;
    margin-top: 12px;
    @media screen and (min-width: 1280px) {
        margin-top: 72px;
    }
}
.wrap--one {
    
    @media screen and (min-width: 768px) {
       display: block;  
       
      
    }

    @media screen and (min-width: 1280px) {
        
        margin-top: 56px;
       
     }
    
}

.wrap--one--b {
    display: flex;
    align-items: flex-end;
    margin-bottom: 28px;
    
}
       
.wrap--one--c {
    display: flex;
    align-items: flex-end;
}



.hh__title {
    font-family: 'Titan One';
    font-style: normal;
    font-size: 26px;
    line-height: 1.15;
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ffffff;
    
    @media screen and (max-width: 767px) {
       margin-top: 136px; 
    }
    @media screen and (min-width: 768px) {
        margin-top: 15px; 
     }
     @media screen and (min-width: 1280px) {
        margin-left: 95px; 
        font-size: 38px;
     }
    
}
.hh__title--big {
    
    font-size: 48px;
    line-height: 0.96;
    color: #d41443;
    @media screen and (min-width: 1280px) {
     
        font-size: 58px;
     }
}

.false-btn__ul {
    display: flex;
    
    @media screen and (min-width: 1280px) {
        margin-left: 95px; 
     }
}
.false-btn__li {
    
    margin-top: 20px;
    border-radius: 20px;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 14px;
    padding-right: 14px;
    &:last-child {
        margin-left: 8px;
    }
 }
 .false-btn__li--pink {
    background-color: #d41443;
    &:focus, &:hover {
        background-color: #c7133e;
    }
 }
 .false-btn__li--white {
    background-color: #ffffff;
    &:focus, &:hover {
        background-color: #ecebeb;
    }
 }

.false-btn__link {
    font-family: 'DM Sans';
font-style: normal;
font-size: 8px;
line-height: 1.3;
display: flex;
align-items: center;
text-align: center;
@media screen and (min-width: 1280px) {
     
    font-size: 14px;
 }

}
.false-btn__link--white {
    color: #ffffff;
}
.false-btn__link--pink {
    color: #d41443;
}




.hh__img--2 {
    display: none;
    @media screen and (min-width: 768px) {
        display: block; 
         width: 168px;
      }  
      @media screen and (min-width: 1280px) {
        width: 293px;
      }
}

.hh__wrap--two {
    display: none;
    @media screen and (min-width: 768px) {
        display: block;  
      }
}

.hh__img--1 {
    display: none;
    @media screen and (min-width: 768px) {
      width: 124px;
      display: block; 
      margin-top: 20px; 
    }
    @media screen and (min-width: 1280px) {
        width: 188px;
      }
    
}

.hh__text {
    display: none; 
    @media screen and (min-width: 768px) {
    display: block; 
    font-family: 'DM Sans';
    font-style: normal;
    font-size: 8px;
    line-height: 1.5;
    color: #ffffff;
    max-width: 114px;
    margin-left: 6px;
    margin-bottom: 7px;
}
    @media screen and (min-width: 1280px) {
         max-width: 200px;
        font-size: 14px;
     }
      
}
.hh__btn--right {
    display: none; 
    @media screen and (min-width: 768px) {
        padding-bottom: 6px;
        padding-left: 6px;
        padding-top: 6px;
        padding-right: 6px;

        background-color: #ffffff;
        border: #ffffff;
        border-radius: 50%;

        display: flex;
        justify-content: center;
      
        
    

        
          }
}
.hh__icon--right {
    fill: #d41443;
}


.overlay {
    position: absolute;  
    z-index: -1;
    background: #ffa5ba;
    border-radius: 50%;
    width: 346px;
    height: 346px;
    @media screen and (max-width: 767px) {
        top: 83px;
        left: 90px;
         
         }
    @media screen and (min-width: 768px) {
        width: 324px;
        height: 322px;
        top: 36px;
        right: 155px;
       
       }
    
    @media screen and (min-width: 1280px) {
        width: 538px;
        height: 538px; 
        top: 56px;
        right: 220px; 
    }
        
}


.hh__img--3 {
   
    @media screen and (max-width: 767px) {
        width: 171px;
    margin-left: 50px;
    margin-top: 10px;
       
       }
    @media screen and (min-width: 768px) {
     width: 143px;
    margin-top: 72px;
     margin-left: 72px;
    
    }
    @media screen and (min-width: 1280px) {
        width: 236px;
        margin-top: 56px;
     margin-left: 119px;
   
       
       }
    
    
    
    
}

.hh__ul--decor {
    
    @media screen and (max-width: 767px) {
        display: none;
    }
}
.decor__li {
    position: relative;
   &:last-child {
       margin-top: 13px;
   }
}

.ellipse {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    @media screen and (min-width: 1280px) {
        width: 30px;
        height: 30px;
      }
}
.hh__numbers {
    font-family: 'Titan One';
    font-style: normal;
    font-size: 28px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    display: block;
    color: #D41443;
    @media screen and (min-width: 1280px) {
        font-size: 46px;
      }
}

.hh__words {
    font-family: 'DM Sans';
    font-style: normal;
    font-size: 8px;
    line-height: 1.25; 
    display: block;   
    color: #ffffff;
    display: flex;
    justify-content: center;
    @media screen and (min-width: 1280px) {
        font-size: 14px;
      }
}















.modal {
     
   

    width: 100vw;
    height: 100vh;
 
   
    position: absolute;
    top: 0;
    z-index: 6;
   
    
    background: #ecebeb;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    
    .modal__img {
        width: 383px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

}





.wrap--one {
   
        @media screen and (max-width: 767px) {
          display: none;
        }
      
        display: flex;
